import React from 'react'
import Header from '../components/EmployerSignup/Header'
import Box from '@mui/material/Box'
import SignupField from '../components/EmployerSignup/SignupField'
import FooterBottom from '../components/Home/FooterBottom'
import WhatsappLink from '../components/EmployerSignup/WhatsappLink'
import { createTheme, ThemeProvider } from '@mui/material';

const theme = createTheme({
  components: {
    MuiOutlinedInput: {
      styleOverrides: {
        notchedOutline: {
          borderRadius: '8px', // custom border-radius value
        },
      },
    },
  },
});

const EmployerSignup = () => {
  return (
    <ThemeProvider theme={theme}>
      <Box>
        <Header />
        <Box display='flex' flexDirection={{ xs: 'column', lg: 'row' }}>
          <Box display='flex' flex='9' justifyContent='center'>
            <SignupField />
          </Box>
          <Box display='flex' flex='3' justifyContent='end'>
            <WhatsappLink />
          </Box>
        </Box>
        <FooterBottom />
      </Box >
    </ThemeProvider>
  )
}

export default EmployerSignup
